import {model}  from './model'


let zoomInArr = document.querySelectorAll('.zoom-in')

export let control = {
    whopper: model.whopperClick('.whopper'),

    scroll: $(window).scroll(function () {
        model.zoomIn(zoomInArr)
    }),

    useInputMask: model.phoneInputMask('.input-mask'),

    tabs: model.tabs('.modal-container-top-left-tab', '.modal-container-bottom-container'),

    showBigModal: model.showModal('.main-button', '.modal'),

    closeBigModal: model.closeModal('.popup', '.modal'),

    closeModalBtn: model.closeModalBtn('.modal-container-top-cross', '.modal'),

    anchors: model.scrollTo('.to-anchor', '#menu', '.whopper'),

    verticalMenuResize: model.mobileMenuResize

};
