

export let model = {
    mobileMenuResize () {
        window.addEventListener('resize', () => {
            // We execute the same script as before
            let vh = window.innerHeight * 0.01;
            document.documentElement.style.setProperty('--vh', `${vh}px`);
        });
    },

    whopperClick (menuBtn) {
        $(menuBtn).click(function () {
            if (!$(menuBtn).hasClass('open-menu')) {
                window.scrollTo({
                    top,
                    behavior: 'smooth'
                })
                $(menuBtn)
                    .addClass('open-menu')
                    .css({
                        animation: '.5s whopperAnimation',
                    })
                setTimeout(function () {
                    $(menuBtn).css({
                        animation: 'none'
                    })
                },500)

                model.slideFromRight('#menu')

                $('body, html').addClass('fixed-page')

            } else {
                $(menuBtn)
                    .removeClass('open-menu')
                    .css({
                        animation: '.5s whopperAnimation',
                    })
                model.slideToRight('#menu')

                $('body, html').removeClass('fixed-page')

                setTimeout(function () {
                    $(menuBtn).css({
                        animation: 'none'
                    })
                },500)
            }
        })
    },

    slideFromRight (item) {
        $(item).addClass('mobile-menu-open')
    },

    slideToRight (item) {
        $(item).removeClass('mobile-menu-open')
    },

    zoomIn (array) {
        array.forEach(item => {
            if (item.getBoundingClientRect().top < 600 && !$(item).hasClass('show')) {
                $(item).addClass('show')
            }
        })
    },

    showModal (btn, modal) {
        $(btn).click(function () {
            $(modal)
                .css({
                display: 'flex',
                opacity: 0
            })
                .animate({
                    opacity: 1
                }, 500)
        })
    },

    closeModal (btn, modal) {
        $(btn).click(function (e) {
            let container = $(btn)
            if (container.has(e.target).length === 0){
                $(modal).fadeOut(500)
            }
        })
    },

    closeModalBtn (btn, modal) {
        $(btn).click(function () {
            $(modal).fadeOut(500)
        })
    },

    phoneInputMask (item) {
        $(item).mask('+375 (00) 000-00-00')
    },

    tabs (tab, containers) {
        $(tab).click(function (e) {
            e.preventDefault()
            if (!$(this).hasClass('selected')) {
                $(containers).removeClass('selected')
                $(tab).removeClass('selected')
                $(this).addClass('selected')
                let currentContainer = $(this).attr("href")
                $(currentContainer).addClass('selected')
            }
        })
    },

    scrollTo (btn, closeMobileMenu, menuBtn) {
        $(btn).click(function (e) {
            e.preventDefault();
            let id  = $(this).attr('href')
            let top = $(id).offset().top;
            $('body,html').animate({scrollTop: top}, 1000);
            model.slideToRight(closeMobileMenu)
            $(menuBtn)
                .removeClass('open-menu')
                .css({
                    animation: '.5s whopperAnimation',
                })
            model.slideToRight('#menu')

            $('body, html').removeClass('fixed-page')

            setTimeout(function () {
                $(menuBtn).css({
                    animation: 'none'
                })
            },500)
        })
    }
};

